import Vue from 'vue'
import Vuex from 'vuex'
// import axios from "axios";

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    chapter: null,
    bookDetailData: null,
    rankingData: []
  },
  mutations: {
    updateChapter: function (state, payload) {
      state.chapter = payload
    },
    updateBookDetailData: function (state, detail) {
      state.bookDetailData = detail
    },
    updateRanking(state, ranking) {
      state.rankingData = ranking;
    }
  },
  actions: {

  },
  modules: {
  }
})
