<template>
  <div class="book-list" v-if="fantasyData.length">
    <BookItem
      v-for="(item, index) in fantasyData"
      :key="item.id"
      :item="item"
      :index="index"
      :isShowMore="isShowMore"
    >
    </BookItem>
    <More @click.native="gotoHandler" v-if="isShowMore" />
    <MarginBottom />
  </div>
</template>

<script>
import BookItem from "@/components/BookItem.vue";
import More from "@/components/More.vue";
import MarginBottom from "./MarginBottom.vue";
export default {
  props: ["fantasyData", "link", "title", "isShowMore"],
  components: { BookItem, More, MarginBottom },
  methods: {
    gotoHandler() {
      //   console.log(this.fantasyData[0]._id);
      let id = this.link;
      // console.log(id);
      this.$emit("aaa", this.fantasyData);
      // this.$route.meta.isShowNav = true;
      this.$router.push({
        path: "/recommend",
        query: {
          id,
          title: this.title,
        },
      });
    },
  },
};
</script>

