<template>
  <div class="sidebar-left">
    <van-sidebar
      v-model="activeKey"
      line-height="2px"
      :swipeable="true"
      :border="true"
    >
      <van-sidebar-item
        :title="item.shortTitle"
        name="a"
        v-for="item in epub"
        :key="item._id"
        @click="switchHandler(item._id)"
      >
      </van-sidebar-item>
    </van-sidebar>
  </div>
</template>

<script>
export default {
  props: ["epub"],
  data() {
    return {
      activeKey: 0,
    };
  },
  computed: {},
  mounted() {
    // this.switchHandler("5a323096fc84c2b8efab2482");
  },
  methods: {
    switchHandler(id) {
      this.axios.get("http://novel.kele8.cn/rank/" + id).then((res) => {
        console.log("sidebar");
        this.$router.push({
          path: "/ranking",
          query: {
            id,
          },
        });
        this.$store.commit("updateRanking", res.data.ranking.books);
      });
    },
  },
};
</script>

<style>
.sidebar-left {
  width: 100%;
}
</style>