<template>
  <div
    class="book-item"
    @click="aaa(item._id)"
    :class="[index == 3 && isShowMore ? 'cerrent' : '']"
  >
    <lazy-component>
      <van-swipe-cell>
        <!-- :num="item.majorCate" -->
        <!-- :title="item.title" -->

        <van-card
          :lazy-load="true"
          name="num"
          :desc="item.shortIntro"
          class="goods-card"
          :thumb="decodeURIComponent(item.cover.slice(7) || item.cover)"
        >
          <template #title>
            <span>{{ item.title }}</span> |
            <span :class="[isSerial ? 'red' : 'green']">{{
              isSerial ? "连载" : "完结"
            }}</span>
          </template>
          <template #price
            ><span>{{ item.author }}</span>
            <span class="split">|</span>
            <span>
              <span style="color: #b93321">{{ item.latelyFollower }}</span>
              <span>人气</span>
            </span>
          </template>
          <template #num>
            {{ item.majorCate }}
          </template>
        </van-card>
      </van-swipe-cell>
    </lazy-component>
  </div>
</template>

<script>
export default {
  props: {
    item: Object,
    index: Number,
    isShowMore: Boolean,
  },
  data() {
    return {
      isSerial: Boolean,
    };
  },
  created() {
    this.axios
      .get("http://novel.kele8.cn/book-info/" + this.item._id)
      .then((result) => {
        // console.log(result.data);
        // this.fantasyData = result.data.ranking.books.slice(0, 4);
        this.isSerial = result.data.isSerial;
      });
  },
  methods: {
    aaa(id) {
      this.$router.push({
        path: "/detail",
        query: {
          id,
        },
      });
    },
  },
};
</script>

<style lang="less" scpoed>
.book-item {
  overflow: hidden;
  .van-card__content {
    border-bottom: 1px solid #ccc;
    padding-bottom: 5px;
  }
  .goods-card {
    margin: 0;
    background-color: white;
    .van-card__thumb {
      width: 65px;
    }
    .van-card__thumb img {
      border-radius: 0;
    }
  }

  .delete-button {
    height: 100%;
  }

  .van-ellipsis {
    overflow: inherit;
    white-space: normal;
    text-overflow: inherit;
  }
  .van-card__desc {
    display: -webkit-box;
    overflow: hidden;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    max-height: none;
  }
  .van-card__num {
    // position: absolute;
    // right: 16px;
    // bottom: 15px;
    border: 1px solid #ff8400;
    color: #ff8400;
    font-style: normal;
    font-size: 12px;
    border-radius: 2px;
    padding: 0 2px;
  }
  .van-card__price {
    span {
      padding: 0 2px;
    }
  }
  .green {
    background-color: green;
    color: #fff;
  }
  .red {
    background-color: #b93321;
    color: #fff;
  }
  // .van-swipe-cell__wrapper {
  //   position: relative;
  // }
  // .van-card__latelyFollower {
  //   position: absolute;
  //   left: 84px;
  //   bottom: 15px;
  //   color: #666;
  //   font-size: 12px;
  //   span {
  //     padding: 0 5px;
  //     i {
  //       font-style: normal;
  //     }
  //   }
  // }
}
.cerrent {
  margin: 0 0 10px 0 !important;
  .van-card__content {
    border-bottom: 0;
  }
}
.cerrent:after {
  content: "";
  display: block;
  border-bottom: 1px solid #ccc;
}
</style>