<template>
  <div class="ranking" v-if="epub">
    <HomeHeader :isGoHome="isGoHome" :myBook="bookDetail">{{
      goback
    }}</HomeHeader>
    <div class="nav">
      <van-tabs
        v-model="activeName"
        line-height="2px"
        :swipeable="true"
        :border="true"
      >
        <van-tab title="出版" name="a">
          <div class="ranking-left">
            <SidebarLeft :epub="epub" />
          </div>
          <div class="ranking-right">
            <RankingItem :epub="epub" />
          </div>
        </van-tab>
        <van-tab title="女频" name="b">
          <div class="ranking-left">
            <SidebarLeft :epub="male" />
          </div>
          <div class="ranking-right">
            <RankingItem :epub="male" />
          </div>
        </van-tab>
        <van-tab title="男频" name="c">
          <div class="ranking-left">
            <SidebarLeft :epub="female" />
          </div>
          <div class="ranking-right">
            <RankingItem :epub="female" />
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import HomeHeader from "@/components/HomeHeader.vue";
import SidebarLeft from "@/components/SidebarLeft.vue";
import RankingItem from "@/components/RankingItem.vue";
export default {
  name: "Ranking",
  data() {
    return {
      bookDetail: "排行榜",
      goback: "⮜ 返回",
      isGoHome: true,
      activeName: "a",
      epub: [],
      male: [],
      female: [],
      isShowLoading: false,
    };
  },
  components: {
    HomeHeader,
    SidebarLeft,
    RankingItem,
  },
  watch: {
    activeName(n) {
      // let time = new Date().getTime();
      let time = 0;
      if (window.localStorage.getItem("rankingState")) {
        let local = JSON.parse(window.localStorage.getItem("rankingState"));
        time = local.time;
      }

      window.localStorage.setItem(
        "rankingState",
        JSON.stringify({ state: n, time: time })
      );
      let a = (type) => {
        if (window.localStorage.getItem(type)) {
          let _data = JSON.parse(window.localStorage.getItem(type));
          this.$store.commit("updateRanking", _data);
          return;
        }
        // console.log(this[type]);
        this.axios
          .get("http://novel.kele8.cn/rank/" + this[type][0]._id)
          .then((res) => {
            // console.log("watch");
            this.$store.commit("updateRanking", res.data.ranking.books);
            window.localStorage.setItem(
              type,
              JSON.stringify(res.data.ranking.books)
            );
          });
      };
      switch (n) {
        case "a":
          a("epub");
          this.$router.push({
            path: "/ranking",
            query: {
              id: "5a323096fc84c2b8efab2482",
            },
          });
          break;
        case "b":
          a("female");
          this.$router.push({
            path: "/ranking",
            query: {
              id: "54d43437d47d13ff21cad58b",
            },
          });
          break;
        case "c":
          a("male");
          this.$router.push({
            path: "/ranking",
            query: {
              id: "54d42d92321052167dfb75e3",
            },
          });
          break;
        default:
          break;
      }
    },
  },
  created() {
    this.isShowLoading = true;
    let state = "";
    if (window.localStorage.getItem("rankingState")) {
      state = JSON.parse(window.localStorage.getItem("rankingState"));
      console.log(state);
      let time1 = new Date().getTime();
      if (time1 > state.time + 1000 * 60 * 60 * 60 * 3) {
        window.localStorage.removeItem("rankingState");
        window.localStorage.removeItem("ranking");
        window.localStorage.removeItem("epub");
        window.localStorage.removeItem("female");
        window.localStorage.removeItem("male");
      }
      state = state["state"];
    }

    console.log(state);
    let _data = [];
    if (state) {
      // console.log(state);

      let _ranking = JSON.parse(window.localStorage.getItem("ranking"));
      this.epub = _ranking.data.epub;
      this.male = _ranking.data.male;
      this.female = _ranking.data.female;

      switch (state) {
        case "a":
          _data = JSON.parse(window.localStorage.getItem("epub"));
          break;
        case "b":
          _data = JSON.parse(window.localStorage.getItem("female"));
          break;
        case "c":
          _data = JSON.parse(window.localStorage.getItem("male"));
          break;
        default:
          break;
      }
      this.isShowLoading = false;
      this.$store.commit("updateRanking", _data);
      this.activeName = state;
      return;
    }
    console.log("11111");
    this.axios.get("http://novel.kele8.cn/rank-category").then((result) => {
      // console.log(result);
      this.epub = result.data.epub;
      this.male = result.data.male;
      this.female = result.data.female;
      window.localStorage.setItem("ranking", JSON.stringify(result));

      this.axios
        .get("http://novel.kele8.cn/rank/" + result.data.epub[0]._id)
        .then((res) => {
          // console.log(res);
          let time = new Date().getTime();
          console.log(time);
          this.isShowLoading = false;

          this.$store.commit("updateRanking", res.data.ranking.books);
          window.localStorage.setItem(
            "rankingState",
            JSON.stringify({
              state: "a",
              time: time,
            })
          );
        });
    });
  },
};
</script>

<style lang="less">
.van-tab__pane {
  display: flex;
  // justify-content: center;
  .ranking-left {
    // width: 100%;
    height: calc(100vh - 94px);
    overflow: scroll;
  }
  .ranking-right {
    flex: 1 !important;
    width: 100%;
    height: calc(100vh - 94px);
    overflow: scroll;
  }
}
// .ranking-left {
//   float: left !important;
//   width: 30% !important;
//   overflow: hidden;
//   .sidebar-left {
//     width: 100% !important;
//   }
// }
.van-card__header {
  margin: 0;
  background-color: white;
  .van-card__thumb {
    width: 65px;
  }
  .van-card__thumb img {
    border-radius: 0;
  }
}
.van-tabs__wrap {
  box-sizing: border-box;
  height: 44px;
  // border: 1px solid #ccc;
  // margin-bottom: -2px;
  .van-tab {
    // padding-bottom: -2px;
  }
}
.ranking {
  height: 100vh;
}
</style>