<template>
  <div class="recently">
    <div class="record">
      <a class="history" @click.prevent="$router.push('/bookHistory')"
        >最近阅读</a
      >
      <a
        href="javascript:void(0);"
        id="J_record"
        class="read"
        @click="
          $router.push({
            path: '/detail',
            query: {
              id: currentReadBook['_id'],
            },
          })
        "
        >{{ currentReadBook.title || "暂无阅读记录" }}</a
      >
    </div>
    <MarginBottom />
  </div>
</template>

<script>
import MarginBottom from "./MarginBottom.vue";
export default {
  components: {
    MarginBottom,
  },
  data() {
    return {
      currentReadBook: {},
    };
  },
  created() {
    if (!window.localStorage.getItem("readBooks")) {
      return;
    }
    this.currentReadBook =
      JSON.parse(window.localStorage.getItem("readBooks"))[0] || {};
  },
};
</script>

<style lang="less" scpoed>
.recently {
  .record {
    // width: 100%;
    // height: 40px;
    padding: 10px 21px;
    display: flex;
    align-items: center;
    a {
      font-size: 12px;
      color: #999;
      padding-left: 10px;
    }
    .history {
      border-right: 1px solid #999;
      padding-right: 10px;
    }
    .read {
      color: #da6161;
    }
  }
}
</style>