<template>
  <div class="search-icon">
    <van-search placeholder="" action-text @click="searchHandler">
      <template #left-icon>
        <van-swipe
          style="height: 34px; line-height: 34px"
          autoplay="3000"
          loop
          vertical
          :show-indicators="false"
          @change="onChange"
        >
          <van-swipe-item v-for="text in swipeText" :key="text">{{
            text
          }}</van-swipe-item>
        </van-swipe>
      </template>
      <template #right-icon>
        <van-icon name="search" @click.stop="clickHandler" />
      </template>
    </van-search>
  </div>
</template>

<script>
// import Search from "@/views/Search.vue";
export default {
  components: {
    // Search,
  },
  data() {
    return {
      value: "",
      swipeText: [
        "修真聊天群",
        "论圣父的垮掉[快穿]",
        "无敌神龙养成系统",
        "开局签到一个首富姐姐",
        "逢春",
      ],
      currentText: "修真聊天群",
    };
  },
  methods: {
    searchHandler() {
      // console.log(this.$router);
      this.$router.push({
        path: "/search",
        // component: Search,
      });
    },
    onChange(index) {
      // console.log(this.swipeText[index]);
      this.currentText = this.swipeText[index];
    },
    clickHandler() {
      console.log("click");
      this.$router.push({
        path: "/search",
        query: { isSearch: true, value: this.currentText },
        // component: Search,
      });
    },
  },
};
</script>

<style lang="less">
.search-icon {
  position: relative;
  .van-swipe {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    .van-swipe-item {
      font-size: 12px;
      color: #999;
    }
  }
}
</style>