<template>
  <div class="home-header">
    <van-nav-bar @click-left="onClickLeft">
      <template #left>
        <slot />
      </template>
      <template #title>
        <div class="sex" v-if="gender">
          <van-tabs
            type="card"
            color="white"
            background="transparent"
            title-active-color="#999"
          >
            <van-tab style="">
              <template #title>
                <!-- 对象传参 -->
                <!-- push({
                  path:'/',
                  query:{
                    gender:male
                  }
                }) -->
                <span
                  @click="$router.push('/?gender=male')"
                  :class="{ active: gender == 'male' }"
                  >男生</span
                >
              </template>
            </van-tab>
            <van-tab style="" class="gender">
              <template #title>
                <span
                  @click="$router.push('/?gender=female')"
                  :class="{ active: gender == 'female' }"
                  >女生</span
                >
              </template>
            </van-tab>
          </van-tabs>
        </div>
        <div v-if="myBook">
          {{ myBook }}
        </div>
      </template>
      <template #right>
        <van-image
          :show-loading="false"
          @click="$router.push('/bookrack')"
          class="van-image_01"
          width="25"
          height="25"
          src="data:image/png;base64,"
        />
        <van-image
          :show-loading="false"
          v-if="isHome"
          @click="$router.push('/me')"
          width="25"
          height="25"
          src="data:image/png;base64,"
        />
        <van-image
          :show-loading="false"
          v-else
          @click="$router.push('/')"
          width="25"
          height="25"
          src="data:image/png;base64,"
        />
      </template>
    </van-nav-bar>
    <!-- <router-view></router-view> -->
  </div>
</template>

<script>
export default {
  props: ["gender", "imgUrl", "myBook", "isShow", "isHome", "isGoHome"],
  data() {
    return {
      isShowBackground: true,
    };
  },
  methods: {
    onClickLeft() {
      if (this.gender) {
        return;
      }
      // console.log(this, this.$route, this.$router);
      this.isGoHome ? this.$router.push("/") : this.$router.go(-1);
    },
  },
};
</script>

<style lang="less">
.home-header {
  position: relative;
  .van-tab {
    border-color: white;
    color: white;
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    border: 0 !important;
  }
  .van-tabs__nav--card {
    margin: 0;
    border-radius: 15px;
    overflow: hidden;
    .van-tab {
      color: white !important;
      background-color: transparent !important;
      .van-tab__text {
        width: 100%;
        height: 100%;
        padding: 0;
        margin: 0;
        text-align: center;
        line-height: 30px;
        .active {
          background: white;
          display: block;
          width: 100%;
          height: 100%;
          color: #999;
        }
      }
    }
  }
  .sex {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100px;
    height: 28px;
    line-height: 28px;
    margin-top: -14px;
    margin-left: -50px;
    z-index: 2;
    // display: flex;
    // justify-content: center;
    a {
      display: block;
      width: 48px;
      float: left;
      list-style: none;
      text-decoration: none;
      color: #fff;
      font-size: 14px;
      text-align: center;
      // border-top-left-radius: 15px;
    }
    a:nth-child(1) {
      border: 1px solid #fff;
      border-top-left-radius: 15px;
      border-bottom-left-radius: 15px;
      // border: 1px solid #fff;
    }
    a:nth-child(2) {
      border: 1px solid #fff;
      border-top-right-radius: 15px;
      border-bottom-right-radius: 15px;
    }
    // a.active {
    //   background-color: #fff;
    //   color: #999;
    // }
  }
  .van-nav-bar {
    position: relative;
    height: 50px;
    background-color: #b83320;
    .van-nav-bar__content {
      height: 50px;
      .van-nav-bar__right {
        .van-image_01 {
          margin-right: 10px;
        }
      }
      .van-nav-bar__right {
        position: relative;
      }

      .van-nav-bar__left {
        position: relative;
        // top: 16px;
        // left: 9px;
        width: 78px;
        height: 18px;
        color: white;
        img {
          width: 100%;
        }
        // background: url(data:image/png;base64,)
        //   no-repeat;
        // background-size: 100% 100%;
      }
    }
  }

  .van-nav-bar__title,
  .van-nav-bar__text {
    color: white;
  }
  // .active {
  //   background-color: #fff;
  //   color: #999 !important;
  // }
}

// @nav-bar-background-color: #b83320;
</style>