<template>
  <div class="home">
    <HomeHeader :gender="gender" :isHome="true">
      <img :src="imgUrl[0]" alt="" />
    </HomeHeader>
    <SearchIcon />
    <!-- <Gender /> -->
    <router-view></router-view>
    <div v-if="gender == 'male'">
      <Male :arrId="mArrId" />
      <NavBar />
      <Recently />
      <RecommendHearder>男生热门</RecommendHearder>
      <BookList
        v-if="maleHotData"
        :fantasyData="maleHotData"
        :link="maleLink[1]"
        :title="'男生热门'"
        :isShowMore="isShowMore"
      />

      <RecommendHearder> 男生完结 </RecommendHearder>

      <BookList
        v-if="fantasyData"
        :fantasyData="fantasyData"
        :link="maleLink[0]"
        :title="'男生完结'"
        :isShowMore="isShowMore"
      />
    </div>
    <div v-else>
      <Female :arrId="fArrId" />
      <NavBar />
      <Recently />
      <RecommendHearder>女生热门</RecommendHearder>
      <BookList
        v-if="readySaleData"
        :fantasyData="readySaleData"
        :link="femaleLink[0]"
        :title="'女生热门'"
        :isShowMore="isShowMore"
      />
      <RecommendHearder>女生完结</RecommendHearder>
      <BookList
        v-if="femaleHotData"
        :fantasyData="femaleHotData"
        :link="femaleLink[1]"
        :title="'女生完结'"
        :isShowMore="isShowMore"
      />
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import HomeHeader from "@/components/HomeHeader.vue";
import BookList from "@/components/BookList.vue";
import SearchIcon from "@/components/SearchIcon.vue";

import NavBar from "@/components/NavBar.vue";
import Recently from "@/components/Recently.vue";
import RecommendHearder from "@/components/RecommendHearder.vue";
import Male from "@/components/Male.vue";
import Female from "@/components/Female.vue";
// import { mapState } from "vuex";
// console.log(mapState);
export default {
  name: "Home",
  components: {
    HomeHeader,
    SearchIcon,
    NavBar,
    Recently,
    RecommendHearder,
    BookList,
    Male,
    Female,
  },
  data() {
    return {
      isShowMore: true,
      isSerial: false,
      imgUrl: [
        "data:image/png;base64,",
      ],
      gender: "male",
      fArrId: [
        "55b9c82a491762a137fc777c",
        "5c75017ffbbc9854793998c2",
        "5ee198afc045c331443b18d8",
      ],
      mArrId: [
        "5dc8bb6e1a9f25ad19391258",
        "536da71c88844484410104a3",
        "508742f0abf1ced53c000024",
      ],
      maleLink: ["564eb878efe5b8e745508fde", "54d42d92321052167dfb75e3"],
      femaleLink: ["5a39ca20fc84c2b8ef82c9ed", "564eb8a9cf77e9b25056162d"],
      // fantasyLink: ,
      // hotLink: ,
      urlStr: "",
      //男生完结
      fantasyData: [],
      //男生最热~
      maleHotData: [],
      //女生最热
      femaleHotData: [],

      //女生畅销榜
      readySaleData: [],
    };
  },
  computed: {
    // ...mapState(["list"]),
    // getList(){
    //   this.urlStr= list
    // }
  },
  methods: {},
  created() {
    //男生
    this.axios
      .get("http://novel.kele8.cn/rank/" + this.maleLink[0])
      .then((result) => {
        // console.log(result);
        this.fantasyData = result.data.ranking.books.slice(0, 4);
      });

    this.axios
      .get("http://novel.kele8.cn/rank/" + this.maleLink[1])
      .then((result) => {
        // console.log(result);
        this.maleHotData = result.data.ranking.books.slice(0, 4);
      });

    // 女生;
    this.axios
      .get("http://novel.kele8.cn/rank/" + this.femaleLink[0])
      .then((result) => {
        this.readySaleData = result.data.ranking.books.slice(0, 4);
      });
    this.axios
      .get("http://novel.kele8.cn/rank/" + this.femaleLink[1])
      .then((result) => {
        this.femaleHotData = result.data.ranking.books.slice(0, 4);
      });
  },
  beforeRouteEnter(to, from, next) {
    // 在渲染该组件的对应路由被 confirm 前调用
    // 不！能！获取组件实例 `this`
    // 因为当守卫执行前，组件实例还没被创建
    next((vm) => {
      // 通过 `vm` 访问组件实例
      vm.gender = to.query.gender || "male";
    });
  },
  beforeRouteUpdate(to, from, next) {
    // 在当前路由改变，但是该组件被复用时调用
    // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
    // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
    // 可以访问组件实例 `this`
    // console.log(to, from);
    // console.log(to.query.gender === this.gender);

    // console.dir(to, from, next);
    this.gender = to.query.gender || "male";
    next();
  },
};
</script>
