import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
// import Male from '@/components/Male.vue'
// import Female from '@/components/Female.vue'

import Book from '@/components/NavBarItem/Book.vue'
import Ranking from '@/components/NavBarItem/Ranking.vue'
import Vip from '@/components/NavBarItem/Vip.vue'
import Pay from '@/components/NavBarItem/Pay.vue'

Vue.use(VueRouter)
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      keepAlive: true,  //代表需要缓存
      isBack: false,
    },
    // redirect: { name: 'Male' },
    // children: [
    //   {
    //     path: '/?gender=male',
    //     name: 'Male',
    //     component: Male,
    //     alias: '/',
    //   },
    //   {
    //     path: '/?gender=female',
    //     name: 'Female',
    //     component: Female
    //   }
    // ]
  },
  {
    path: '/me',
    name: 'Me',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "me" */ '@/views/Me.vue')
  },
  {
    path: '/search',
    name: 'Search',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "search" */ '@/views/Search.vue')
  },
  {
    path: "/book",
    component: Book
  },
  {
    path: "/ranking",
    component: Ranking,
    children: [
      {
        path: '/ranking/:id',
        props: true
      }
    ]
  },
  {
    path: "/vip",
    component: Vip
  },
  {
    path: "/pay",
    component: Pay
  }
  , {
    path: "/recommend",
    component: () => import(/* webpackChunkName: "recommend" */ '@/views/Recommend.vue'),
    children: [
      {
        path: "/recommend/:id",
        props: true
      }
    ]
  },
  {
    path: "/bookrack",
    component: () => import(/* webpackChunkName: "bookrack" */ '@/views/Bookrack.vue'),
  },
  {
    path: '/bookHistory',
    component: () => import(/* webpackChunkName: "bookHistory" */ '@/views/BookHistory.vue'),
  },
  {
    path: "/detail",
    component: () => import(/* webpackChunkName: "detail" */ '@/views/Detail.vue'),
    chlidren: [
      {
        path: "/detail/:id",
        props: true
      },

    ],
  },
  {
    path: "/book/:id",
    component: () => import(/* webpackChunkName: "Read" */ '@/views/Read.vue'),
  }
  // 

]

const router = new VueRouter({
  routes
})

export default router
