<template>
  <div class="ranking-item">
    <lazy-component>
      <van-card
        v-for="(item, index) in rankingData"
        :key="item._id"
        @click="aaa(item._id)"
        :title="index + 1 + '.' + item.title"
        :thumb="decodeURIComponent(item.cover.slice(7))"
        :lazy-load="true"
      >
        <template #desc>
          <!-- 最多显示两行 -->
          <div class="van-multi-ellipsis--l2">
            {{ item.shortIntro }}
          </div>
        </template>
        <template #price>
          <span>{{ item.author }}</span>
        </template>
        <template #num>
          <span>{{ item.majorCate }}</span>
        </template>
      </van-card>
    </lazy-component>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  props: ["epub"],
  computed: {
    ...mapState(["rankingData"]),
  },
  methods: {
    aaa(id) {
      this.$router.push({
        path: "/detail",
        query: {
          id,
        },
      });
    },
  },
};
</script>

<style lang="less">
.ranking-item {
  width: 100%;
  .van-card__title {
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 5px;
    -webkit-line-clamp: 1;
  }
}
</style>